import {
  Component,
  EventEmitter,
  Input,
  Output,
  ViewChild,
} from "@angular/core";
import { SuggestionLookupTableModalComponent } from "@shared/components/suggestion-lookup-table-modal/SuggestionLookupTableModalComponent";
import { GridApi, GridReadyEvent, ColDef } from "ag-grid-community";
import { BsModalService, BsModalRef } from "ngx-bootstrap/modal";

@Component({
  selector: "app-previous-job-grid",
  templateUrl: "./previous-job-grid.component.html",
})
export class PreviousJobGridComponent {
  protected gridApi: GridApi;
  rowSelection: string;
  rowCount: number;
  // rowData: any[] = [];
  protected setParms;
  target: string;
  @Input() rowData: any;

  suggestionModalRef: BsModalRef;
  @ViewChild("SuggestionLookupTableModalComponent")
  SuggestionLookupTableModalComponent: SuggestionLookupTableModalComponent;
  @Output() outputLicenseArray: EventEmitter<any[]> = new EventEmitter<any[]>();

  constructor(private suggestionService: BsModalService) {}

  colDefs: ColDef[] = [
    {
      headerName: "SrNo",
      editable: false,
      field: "srNo",
      sortable: true,
      valueGetter: "node.rowIndex+1",
      suppressSizeToFit: true,
    },

    {
      field: "startDate",
      headerName: "Start Date",
      editable: true,
      cellEditor: "agDateCellEditor",
      valueFormatter: (params) => {
        if (params.value) {
          const date = new Date(params.value);
          return date.toISOString().split("T")[0]; // Format date as yyyy-mm-dd
        }
        return "";
      },
      valueParser: (params) => {
        return new Date(params.newValue); // Parse string to Date object
      },
    },
    {
      field: "endDate",
      headerName: "End Date",
      editable: true,
      cellEditor: "agDateCellEditor",
      valueFormatter: (params) => {
        if (params.value) {
          const date = new Date(params.value);
          return date.toISOString().split("T")[0]; // Format date as yyyy-mm-dd
        }
        return "";
      },
      valueParser: (params) => {
        return new Date(params.newValue); // Parse string to Date object
      },
    },
    {
      headerName: "Experience",
      editable: false,
      field: "experience",
      resizable: true,
      suppressSizeToFit: true,
    },
    {
      headerName: "Title",
      editable: true,
      field: "title",
      resizable: true,
      suppressSizeToFit: true,
    },
    {
      headerName: "Starting Salary",
      editable: true,
      field: "startingSalary",
      resizable: true,
      suppressSizeToFit: true,
    },
    {
      headerName: "Ending Salary",
      editable: true,
      field: "endingSalary",
      resizable: true,
      suppressSizeToFit: true,
    },
    {
      headerName: "Contact",
      editable: true,
      field: "contact",
      resizable: true,
      suppressSizeToFit: true,
    },
    {
      headerName: "Reason of Leaving",
      editable: true,
      field: "reasonForLeaving",
      resizable: true,
      suppressSizeToFit: true,
    },
  ];

  onGridReady(params: GridReadyEvent) {
    this.gridApi = params.api;
    this.rowSelection = "multiple";
  }

  onAddRow() {
    const newItem = {};
    this.colDefs.forEach((colDef) => {
      newItem[colDef.field] = "";
    });
    this.gridApi.applyTransaction({ add: [newItem] });
    this.rowCount = this.gridApi.getDisplayedRowCount();
  }

  onRemoveSelected() {
    const selectedNodes = this.gridApi.getSelectedNodes();
    this.gridApi.applyTransaction({
      remove: selectedNodes.map((node) => node.data),
    });
    this.rowCount = this.gridApi.getDisplayedRowCount();
  }

  addIconCellRendererFunc() {
    debugger;
    return '<i class="fa fa-plus-circle fa-lg" style="color: green;margin-left: -9px;cursor: pointer;" ></i>';
  }

  cellClicked(params) {
    debugger;
    switch (params.column["colId"]) {
      case "addLicense":
        this.setParms = params;
        this.openSelectLicenseType();
        break;
      default:
        break;
    }
  }
  cellValueChanged(params) {
    if (
      params.colDef.field == "startDate" ||
      params.colDef.field == "endDate"
    ) {
      if (params.data.startDate && params.data.endDate) {
        const startDate = new Date(params.data.startDate);
        const endDate = new Date(params.data.endDate);

        // Calculate the difference in time
        const timeDiff = endDate.getTime() - startDate.getTime();
        // Calculate the difference in days
        const daysDiff = Math.ceil(timeDiff / (1000 * 3600 * 24));
        const yearsDiff = daysDiff / 365;

        // Update the experience field
        params.data.experience = yearsDiff.toFixed(1);
        this.gridApi.refreshCells();
      }
    }
  }
  openSelectLicenseType() {
    debugger;
    this.target = "LicenseType";
    const initialState: any = {
      target: this.target,
    };
    this.suggestionModalRef = this.suggestionService.show(
      SuggestionLookupTableModalComponent,
      { initialState }
    );

    this.suggestionModalRef.content.saveSuggestion.subscribe((result) => {
      debugger;
      this.setParms.data.licenseTypeId = result.id;
      this.setParms.data.licenseTypeName = result.name;
      this.gridApi.refreshCells();
    });
  }

  onSave() {
    debugger;
    var rowData = [];
    this.gridApi.forEachNode((node) => {
      rowData.push(node.data);
    });
    this.outputLicenseArray.emit(rowData);
  }
}
